import React, { useEffect, useRef, useState } from 'react'
import Box from 'components/Box'
import DramDataTable from './DramDataTable'
import PageSimulator from './PageSimulator'

const DramDownloader = ({
  headerKeys,
  pages,
  headerRow,
  doubleHeader,
  bodyData,
  quater,
  year,
  type,
  half,
  noteText,
  notes,
  name,
  onDownloaded,
}) => {
  const [page, setPage] = useState(0)
  const downloadRef = useRef()
  useEffect(() => {
    if (downloadRef.current) {
      downloadRef.current().then(() => {
        if (page === pages.length - 1) {
          onDownloaded()
        } else {
          setPage(page + 1)
        }
      })
    }
  }, [page])
  return (
    <Box overflow="hidden" width="0" height="0">
      <Box width="1920px">
        <PageSimulator key={page} name={`${name}-${type}y-${page + 1}`} downloadRef={downloadRef}>
          <DramDataTable
            headerKeys={headerKeys}
            data={pages[page]}
            headerRow={headerRow}
            doubleHeader={doubleHeader}
            bodyData={bodyData}
            quater={quater}
            year={year}
            type={type}
            half={half}
            noteText={noteText}
            // noteHeader={noteHeader}
            notes={notes}
          />
        </PageSimulator>
      </Box>
    </Box>
  )
}

export default DramDownloader
