import groupBy from 'lodash/groupBy'
import last from 'lodash/last'
import upperFirst from 'lodash/upperFirst'
import React, { useMemo } from 'react'
import styled from 'styled-components'

import Box from 'components/Box'
import PageSimulator from 'containers/PageSimulator'
import PageTitle from 'containers/PageTitle'
import sortSize from 'utils/sortSize'
import Flex from 'components/Flex'
import NoteBox from 'containers/NoteBox'
import Text from 'components/Text'

const StyledTable = styled.table`
th, td {
  border: 3px solid white;
}
background: linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3));
`
const parseVolt = /(\d+(.\d+)?)/
const parseSize = s => /^(\d+)(\D+)$/.exec(s)

const voltSort = (a, b) => parseVolt.exec(b)?.[1] - parseVolt.exec(a)?.[1]

const CompactPortfolio = ({
  name,
  colGroupKey,
  rowGroupKey,
  mainKey,
  timeKey,
  timeColor,
  data,
}) => {
  const rows = useMemo(() => {
    const flatData = data.reduce((all, d) => all.concat(d[1]), [])
    const rowGroup = groupBy(flatData, rowGroupKey)
    return sortSize(Object.keys(rowGroup))
  }, [data, rowGroupKey])
  const groupedByCol = useMemo(() => data.map(([, d]) => {
    const grouped = groupBy(d, colGroupKey)
    return Object.entries(grouped).sort(voltSort)
  }), [data, colGroupKey])

  return (
    <PageSimulator name={`${name}-portfolio-table`}>
      <Box>
        <PageTitle>{`${upperFirst(name)} Memery Portfolio`}</PageTitle>
        <Box
          position="absolute"
          left="86px"
          top="125px"
          right="86px"
        >
          <StyledTable >
            <thead>
              <tr>
                {[['Product', 1]].concat(data.map((d, i) => [d[0], groupedByCol[i].length])).map(([h, colSpan], i) => (
                  <Box
                    as="th"
                    key={i}
                    bg="#c63b38"
                    fontSize="25px"
                    fontWeight="900"
                    color="white"
                    colSpan={colSpan}
                    px="1em"
                    py="0.5em"
                    whiteSpace="nowrap"
                    style={{
                      borderLeftWidth: i > 0 && '10px',
                    }}
                  >{h}</Box>
                ))}
              </tr>
              <tr>
                <Box
                  fontWeight="900"
                  fontSize="15px"
                  as="th"
                  color="white"
                  bg="#848484"
                  py="0.25rem"
                >Voltage/<br />Density</Box>
                {data.map}
                {groupedByCol.reduce((all, g, i) => {
                  return all.concat(g.map(([h], j) => (
                    <Box
                      as="th"
                      key={i + h}
                      fontWeight="700"
                      fontSize="23px"
                      bg="#cacaca"
                      width="197px"
                      style={{
                        borderLeftWidth: j === 0 && '10px',
                      }}
                    >
                      {h}
                    </Box>
                  )))
                }, [])}
              </tr>
            </thead>
            <tbody>
              {rows.map((row) => {
                const res = parseSize(row)
                return (
                  <tr key={row}>
                    <Box
                      as="td"
                      bg="#cacaca"
                      fontSize="19px"
                      fontWeight="bold"
                      textAlign="center"
                      height="58px"
                    >
                      {res ? `${res[1]} ${res[2]}` : row}
                    </Box>
                    {groupedByCol.reduce((all, g, i) => {
                      return all.concat(g.map(([, proucts], j) => (
                        <Box
                          as="td"
                          key={`${i}${j}`}
                          fontWeight="500"
                          fontSize="19px"
                          height="58px"
                          width="197px"
                          style={{
                            borderLeftWidth: j === 0 && '10px',
                          }}
                          px="0.5em"
                          // bg="#f9f9f9"
                        >
                          {proucts.filter(p => p[rowGroupKey] === row).map((p, k) => (
                            <Box key={k} fontFamily="'Noto Sans Mono', monospace" lineHeight="1.25" whiteSpace="nowrap">
                              {p[mainKey]}
                              {p[timeKey] && (
                                <Box
                                  verticalAlign="middle"
                                  display="inline-block"
                                  px="0.25em"
                                  fontSize="15px"
                                  fontWeight="900"
                                  color="white"
                                  bg={timeColor}
                                  ml="0.5em"
                                >
                                  {p[timeKey]}</Box>
                              )}
                            </Box>
                          ))}
                        </Box>
                      )))
                    }, [])}
                  </tr>
                )
              })}
            </tbody>
          </StyledTable>
          <Flex.HStack mt="16px" justifyContent="flex-end" alignItems="center" spacing="16px">
            <NoteBox bg={timeColor}>{last(timeKey.split('.'))}</NoteBox>
            <NoteBox bg="#9fa0a0">NOTE</NoteBox>
            <Text>Roapmaps are subject to change.</Text>
          </Flex.HStack>
        </Box>
      </Box>
    </PageSimulator>
  )
}

export default CompactPortfolio
