import groupBy from 'lodash/groupBy'
import merge from 'lodash/merge'
import React, { useCallback, useMemo } from 'react'
import { useImmer } from 'use-immer'
import styled from 'styled-components';

import Box from 'components/Box'
import Flex from 'components/Flex'
import Text from 'components/Text'
import Input from 'components/Input';
import PortfolioTableRender from './PortfolioTableRender'
import sortSize from 'utils/sortSize'
import CompactPortfolio from './CompactPortfolio';

const mainKeyFormat = d => d.substring(1).replace(/\[[^\]]+\]/g, '')
const mainKey = 'Product'
const groupKey = 'Product Line'
const densityKey = 'Density'

const subGroupKey = {
  dram: 'Technology Node (nm)',
  flash: 'Voltage',
}

const timemark = {
  dram: 'Portfolio.MP',
  flash: 'Portfolio.Sample',
}

const timeColors = {
  dram: '#a4ccdb',
  flash: '#2f6eba',
}

const StyledTable = styled.table`
width: 100%;
th {
  border-bottom: 1px solid #ccc;
}
td, th {
  padding: 0.25em;
}
td:first-of-type {
  width: 5em;
}
input {
  max-width: 3em;
}
`

const lowerMatch = (a, b) => a.toLowerCase() === b.toLowerCase()

const GroupTable = ({ label, onSetGroup, groups, items }) => {
  return (
    <StyledTable>
      <thead>
        <tr>
          <th></th>
          <th>{label}</th>
        </tr>
      </thead>
      <tbody>
        {items.map((item, i) => (
          <tr key={i} p="0.5em">
            <td>
              {item}
            </td>
            <td>
              <Input
                type="number"
                min="0"
                value={groups?.[item] || '1'}
                onChange={e => onSetGroup({ [item]: e.target.value })}
              />
            </td>
          </tr>
        ))}
      </tbody>
    </StyledTable>
  )
}

const GroupCol = ({
  data,
  groupName,
  name,
  colGroup,
  onSetColGroup,
  rowGroup,
  onSetRowGroup,
}) => {
  const colGroupItems = useMemo(() => Object.keys(groupBy(data, subGroupKey[name])), [data, name])
  const rowGroupItems = useMemo(() => sortSize(Object.keys(groupBy(data, densityKey))), [data])
  return (
    <Box border="1px solid" p="0.5em">
      <Box textAlign="center">{groupName}</Box>
      <hr />
      <GroupTable
        label="Col"
        onSetGroup={onSetColGroup}
        groups={colGroup}
        items={colGroupItems}
      />
      <Box height="1em" />
      <GroupTable
        label="Row"
        onSetGroup={onSetRowGroup}
        groups={rowGroup}
        items={rowGroupItems}
      />
    </Box>
  )
}

const PortfolioPage = ({ data, name, variant, headerKeys, filterKey }) => {
  const getMatchAccesor = useCallback((key) => {
    const keyMap = key.split('.')
    return keyMap.reduce((prev, k) => {
      const found = prev.find(({ Header }) => lowerMatch(Header, k))
      if (found?.columns) return found?.columns
      return found
    }, headerKeys)
  }, [headerKeys])
  const extractedData = useMemo(() => {
    const theFilter = getMatchAccesor(filterKey)
    const keys = [
      groupKey,
      densityKey,
      subGroupKey[name],
      timemark[name],
    ]
    const valueGetters = keys.map(getMatchAccesor)
    const mainKeyIndex = data[0].findIndex(d => lowerMatch(d, mainKey))
    return data.slice(2).filter(d => !theFilter || theFilter.accessor(d) === 'TRUE').reduce((all, d, j) => {
      return all.concat(
        d[mainKeyIndex].split('\n').map((m) => {
          return Object.fromEntries([
            [mainKey, mainKeyFormat(m)],
            ...keys.map((k, i) => {
              let v = valueGetters[i]?.accessor?.call(null, d)
              if (v === '' && k !== groupKey && k !== timemark[name]) {
                v = all[j - 1]?.[k]
              }
              return [k, v]
            })
          ])
        })
      )
    }, []).filter(d => d[groupKey])
  }, [data, name, filterKey, getMatchAccesor])
  const groupedData = useMemo(() => groupBy(extractedData, groupKey), [extractedData])
  const productGroups = useMemo(() => Object.keys(groupedData).map(g => g.trim()), [groupedData])
  const [pickedGroup, setPickedGroup] = useImmer({})
  const pickedGroupArr = useMemo(() => Object.keys(pickedGroup).filter((k) => pickedGroup[k]), [pickedGroup])
  const [colGroup, setColGroup] = useImmer({})
  const [rowGroup, setRowGroup] = useImmer({})
  return (
    <>
      <hr />
      <Text>Product Lines:</Text>
      <Box mx="-0.5em">
        {productGroups.map((group) => group && (
          <Box as="label" display="inline-block" m="0.5em">
            <input
              type="checkbox"
              name="group"
              checked={pickedGroup[group]}
              onChange={(e) => setPickedGroup(draft => {
                draft[group] = e.target.checked
              })}
            />
            <Text as="sapn" ml="0.25rem">{group}</Text>
          </Box>
        ))}
        {pickedGroupArr.length > 0 && (
          variant === 'compact' ? (
            <CompactPortfolio
              data={pickedGroupArr.map(g => [g, groupedData[g]])}
              colGroupKey={subGroupKey[name]}
              rowGroupKey={densityKey}
              mainKey={mainKey}
              timeKey={timemark[name]}
              timeColor={timeColors[name]}
              name={name}
            />
          ) : (
            <>
              <Text my="1em">利用下方Col跟Row可以做排版調整，同樣數字代表同一組，數字0代表隱藏</Text>
              <Flex.HStack my="1em" sapcing="1em">
                {pickedGroupArr.map(g => (
                  <GroupCol
                    key={g}
                    data={groupedData[g]}
                    groupName={g}
                    name={name}
                    colGroup={colGroup[g]}
                    onSetColGroup={d => setColGroup(draft => {
                      draft = merge(draft, { [g]: d })
                    })}
                    rowGroup={rowGroup[g]}
                    onSetRowGroup={d => setRowGroup(draft => {
                      draft = merge(draft, { [g]: d })
                    })}
                  />
                ))}
              </Flex.HStack>
              <Box mt="1em">
                <PortfolioTableRender
                  data={pickedGroupArr.map(g => [g, groupedData[g]])}
                  colGroup={colGroup}
                  rowGroup={rowGroup}
                  colGroupKey={subGroupKey[name]}
                  rowGroupKey={densityKey}
                  mainKey={mainKey}
                  timeKey={timemark[name]}
                  timeColor={timeColors[name]}
                  name={name}
                />
              </Box>
            </>
          )
        )}
      </Box>
    </>
  )
}

export default PortfolioPage
