import React, { useState, useMemo } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import { getMonth, getQuarter, getYear } from 'date-fns';
import { compact, concat, floor, isNumber, range, set, size } from 'lodash'
import { Link as GatsbyLink } from 'gatsby'

import Container from '../components/Container'
import Box from '../components/Box'
import Flex from '../components/Flex'
import Link from '../components/Link'
import Button from '../components/Button'
import Pagination from '../components/Pagination'
import { responsive } from '../components/ThemeProvider/theme';

import DramDataTable from './DramDataTable'
import PortfolioPage from './PortfolioPage'
import SheetLoader from './SheetLoader'

// import CompareSummary from './CompareSummary';
import useTableHeader from 'hooks/useTableHeader';
import PageSimulator from './PageSimulator';
import DramDownloader from './DramDownloader';

// const colorKeys = [{}, {}]
