let group = []
const replaceSup = (t) => String(t).replace(/\[(\d+)\]/g, (...d) => `<sup>${d[1]}</sup>`)
const noteBg = { mp: '#A4CCDB', cs: '#4D9185', es: '#DC888A', sample: '#2F6EBA' }
const bodybg = ['#B1B1B2', '#CACACA', '#E8E8E8']
const noteFunc = (noteArr) => {
  const groups = noteArr.reduce((g, n) => {
    if (n.startsWith('-')) {
      const lastGroup = g[g.length - 1]
      lastGroup[1] = lastGroup[1] || []
      lastGroup[1].push(n.substring(1))
    } else {
      g.push([n])
    }
    return g
  }, [])
  return (
    <Box as="ul" pl="1em">
      {groups.map((g, i) => (
        <li key={i}>
          <span dangerouslySetInnerHTML={{ __html: replaceSup(g[0]) }} />
          {g[1] && noteFunc(g[1])}
        </li>
      ))}
    </Box>
  )
}

const DramDataTable = ({
  data,
  notes,
  bodyData,
  quater,
  year,
  type,
  headerRow,
  headerKeys,
  noteText,
  feature,
  name,
  scale,
}) => {
  const [nmGroup, setNmGroup] = useState([]);
  const numberIndex = useMemo(() => headerRow.reduce((res, d, i) => {
    if(!res && isNumber(d.Header)) {
      res = i
    }
    return res
  }, 0), [headerRow])
  const headerWidth = useMemo(() => name === 'mcp' ? ['7rem', '12rem', '12rem', '12rem'] : numberIndex > 3 ? ['6.9rem', '6.9rem', '10.4rem', '13.8rem'] : ['9rem', '12.75rem', '16.25rem'] ,[numberIndex])
  const tableRef = useRef()
  const timelineData = useMemo(() => {
    const dateGetter = notes.map(key => headerKeys.find(d => d.Header === key))
    if (dateGetter.some(d => !d)) return []
    const timeline = data.map(d => dateGetter.map(({ columns }) => columns.map(({ accessor }, i) => {
      const dStr = accessor(d)
      return parseDate(dStr, i)
    })))
    return timeline
  }, [data, headerKeys, notes])
  // const rowSpans = useMemo(() => bodyData?.map(d => d[0]).reverse().reduce((res, h, i, { length }) => {
  //   if (h) {
  //     if (group.length) {
  //       set(res, [length - i - 1], group.length + 1)
  //       group = []
  //     } else {
  //       set(res, [length - i - 1], 1)
  //     }
  //   } else {
  //     group.push(i)
  //   }
  //   return res
  // }, {}), [bodyData])
  const rowSpansfuc = (key) => bodyData?.map(d => d[key]).reverse().reduce((res, h, i, { length }) => {
    if (h) {
      if (group.length) {
        set(res, [length - i - 1], group.length + 1)
        group = []
      } else {
        set(res, [length - i - 1], 1)
      }
    } else {
      group.push(i)
    }
    return res
  }, {})
  // console.log(data)
  useEffect(() => {
    if (tableRef.current) {
      const masterBox = tableRef.current.getBoundingClientRect()
      const cells = tableRef.current.querySelectorAll('.timeline-cell[data-nm')
      if (cells.length) {
        const cellGroup = []
        cells.forEach(cell => {
          const nm = cell.dataset.nm
          const boundBox = cell.getBoundingClientRect()
          const boxCenter = (boundBox.top + boundBox.height / 2 - masterBox.top) / scale
          let currentGroup = cellGroup[cellGroup.length - 1]
          if (nm !== currentGroup?.nm) {
            currentGroup = { nm, left: (boundBox.right - masterBox.left) / scale }
            cellGroup.push(currentGroup)
          }
          currentGroup.top = Math.min(currentGroup.top || boxCenter, boxCenter)
          currentGroup.bottom = Math.max(currentGroup.bottom || boxCenter, boxCenter)
        })
        setNmGroup(cellGroup)
      }
    }
  }, [bodyData])
  // console.log(concat(bodybg, range(3).fill('#E8E8E8')))
  return (
    <Box position="relative" px="3.75em" width="1920px" height="1080px" ref={tableRef}>
      {nmGroup.length > 0 && (
        <Box.FullAbsolute>
          {nmGroup.map(({ nm, top, bottom, left }, i) => (
            <Flex
              key={nm + i}
              position="absolute"
              style={{ top, height: bottom - top, left }}
              border="1px solid"
              borderLeft="none"
              alignItems="center"
            >
              <Box
                style={{
                  writingMode: 'vertical-rl',
                  textRrientation: 'mixed',
                }}
                transform="translateX(120%)"
              >
                {nm}
              </Box>
            </Flex>
          ))}
        </Box.FullAbsolute>
      )}
      <PageTitle>{data[0][0]}</PageTitle>
      <Flex pt="7em">
        <StyledTable flex={1} style={{ position: "relative", background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}>
          <thead style={{ fontSize: '1.25rem' }}>
            <tr>
              {headerRow.map(({ Header, columns }, i) => (
                <Box as="th" style={{ borderRight: i == (numberIndex - 1) && '10px solid' }} height="44px" width={headerWidth[i]} color="white" bg={isNumber(Header) ? '#C63B38' : '#234B69'} rowSpan={!columns && 2} colSpan={columns?.length || 1} key={i}>{Header}</Box>
              ))}
            </tr>
            <tr>
              {headerRow.map(({ columns }, k) => columns?.map(({ Header }, i) => (
                <Box as="th" style={{ borderRight: (name == 'mcp' && k == 3 && i == 1) && '10px solid white' }} height="44px" bg="#CACACA" width="66px" key={i}>{Header}</Box>
              )))}
            </tr>
          </thead>
          {/* Apply the table body props */}
          <tbody style={{ fontSize: '1.125rem', height: '23em' }}>
            {bodyData.map((body, i) => (
              <Box as="tr" key={i}>
                {body.map((d, k) => d && (isObject(d) ? (
                  <Box
                    as="td"
                    colSpan={d.col}
                    // style={{ background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}
                    key={k}
                    position="relative"
                    overflow="visible"
                    className="timeline-cell"
                    data-nm={d.nm || undefined}
                    style={rowSpansfuc(0)[i] ? { borderBottom: 'none', borderLeft: '10px solid white' } : { borderTop: 'none', borderBottom: 'none', borderLeft: '10px solid white' }}
                  >
                    <DramTimeline
                      data={timelineData[i]}
                      startYear={year}
                      startQuarter={type === 3 ? quater : 1}
                      yearSpan={type}
                      colors={notes.map(n => noteBg[n.toLocaleLowerCase()])}
                    />
                    <Box position="relative" pl="8em">
                      <span
                        dangerouslySetInnerHTML={{ __html: replaceSup(d.text) }}
                      />
                      {d.nrnd && <Text.Inline fontWeight="bold" color="#234b69" pl="1em">*NRND</Text.Inline>}
                    </Box>
                  </Box>
                ) : (
                  <Box
                    as="td"
                    fontWeight={!k && 'bold'}
                    bg={name === 'mcp' ? concat(bodybg, range(3).fill('#E8E8E8'))[k]  : concat(range(numberIndex - bodybg.length).fill('#B1B1B2'), bodybg)[k]}
                    textAlign={name === 'mcp' ? 'center' : k === (numberIndex - 1) ? 'left' : 'center'}
                    rowSpan={rowSpansfuc(k)[i]}
                    // style={{ borderRight: (k - (headerRow.length - body.length)) == (numberIndex - 1) && '10px solid white'}}
                    key={k}
                  >
                    <Box
                      px={name === 'mcp' ? '0' : "1em"}
                      dangerouslySetInnerHTML={{ __html: replaceSup(d) }}
                    />
                  </Box>
                )))}
              </Box>
            ))}
          </tbody>
        </StyledTable>
        <Flex flexDirection="column" ml="4.25em" width="19.375em">
          <Box border="1.5px solid white" bg="white" p="0.5rem">　</Box>
          <Flex mb="1em">
            {notes.map((d, i) => (
              <Box fontSize="1.25rem" width="100%" fontWeight="bold" textAlign="center" bg={noteBg[d.toLocaleLowerCase()]} color="white" p="0.5rem" key={i}>{d}</Box>
            ))}
          </Flex>
          {!headerRow.some(d => d.Header === 'Description') && (
            <>
              <Box bg="#9FA0A0" fontSize="1.125rem" textAlign="center" color="white" p="0.5rem" fontWeight="bold">FEATURE</Box>
              <Box p="0.5rem" style={{ background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}>
                {feature[0] && noteFunc(feature)}
              </Box>
            </>
          )}
          <Box bg="#9FA0A0" fontSize="1.125rem" textAlign="center" color="white" p="0.5rem" fontWeight="bold">NOTE</Box>
          <Box flex={1} p="0.5rem" style={{ background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}>
            {noteText[0] && noteFunc(noteText)}
          </Box>
        </Flex>
      </Flex>
    </Box>
  )
};

export default DramDataTable;
